import React, { Component } from 'react';
import App from './App';
import '../styles/appWrapper.css'
import {getConfig} from '../helpers';
import request, {POST, API_URL} from '../requests';
import  qs from 'qs';

class AppWrapper extends Component {

    constructor(props) {
        super(props)
        this.state = {
            config: {}
        };
        this.updateConfig = this.updateConfig.bind(this);
    }

    componentWillMount(){
        this.updateConfig();
    }

    updateConfig(){
        getConfig().then((cfg) => {
            this.setState({
                config: cfg
            })
        }).catch(error => {
            console.log(error);
            request(POST, API_URL + 'errorMessage/', qs.stringify({message: error.response.data}), {
                headers: { "x-requested-with": 'app-creator-tool' }
            })
        });
    }

    render() {
        return (
            <div className="app-container" >
                {this.state.config.identity ? 
                    <App config={this.state.config} refreschConfig={this.updateConfig}/>
                    :
                    <div className="config-not-found">Config Not Found</div>
                }
            </div>
        )
    }
}

export default AppWrapper;


