import request, {GET, POST, API_URL} from './requests';
const qs = require('qs')

export const hideToggle = (elementId, arrowId) => {
    const element = document.getElementById(elementId);
    const arrow = document.getElementById(arrowId);

    if (element.clientHeight > 0) {
        element.style.height = 0;
        arrow.style.transform = 'rotateZ(-90deg)';
    } else {
        element.style.height = element.scrollHeight + 'px';
        arrow.style.transform = 'rotateZ(0deg)';
    };
};

export const addHeight = (elementId) => {
    const element = document.getElementById(elementId);
    element.style.height = element.scrollHeight + 'px';
};

export const getConfig = async () => {
    const response = await request(GET, API_URL + 'config/service', {}, {
        headers: { "x-requested-with": 'app-creator-tool' }
    }).then(resp => Promise.resolve(resp.data));
    return response;
};

export const updateConfig = async (config, callback) => {
    await request(POST, API_URL + 'config/service', config, {
        headers: { "x-requested-with": 'app-creator-tool' }
    }).then((resp => {
        Promise.resolve(resp)
        if(resp.status === 200) {
            callback();
        }
    }))
    .catch(error => {
        request(POST, API_URL + 'errorMessage/', qs.stringify({message: error.response.data}), {
            headers: { "x-requested-with": 'app-creator-tool' }
        })
    });
};

export const openDialogBoxKey = () => {
    return request(GET, API_URL + 'openDialogBoxFile/', {}, {
        headers: { "x-requested-with": 'app-creator-tool' }
    });
};



