import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../styles/App.css';
import {hideToggle, addHeight, updateConfig, openDialogBoxKey} from '../helpers';
import folderIcon from './folder-icon.svg';
import request, {POST, API_URL} from '../requests';
import  qs from 'qs';

class App extends Component {

  constructor(props) {
    super(props)
    this.state = {
      identity: this.props.config.identity,
      signing: this.props.config.signing,
      connection: this.props.config.connection
    };
    this.handleOnBlur = this.handleOnBlur.bind(this);
    this.handleBrowse = this.handleBrowse.bind(this);
}

  componentDidMount() {
    addHeight('identity');
    addHeight('signing');
    addHeight('connection');
  }

  handleOnBlur = (section, key, value) => {
    this.setState(prevState => ({
      ...prevState,
      [section]: {
        ...prevState[section],
        [key]: value
      }
    }), () => updateConfig(this.state, this.props.refreschConfig));
  }

  handleBrowse() {
    openDialogBoxKey().then((response) => {
        this.setState(prevState => ({
          ...prevState,
          signing: {
            ...prevState.signing,
            privateKeyPath: response.data.fsPath
          },
        }), () => updateConfig(this.state, this.props.refreschConfig))
      }).catch(error => {
        request(POST, API_URL + 'errorMessage/', qs.stringify({message: error.response.data}), {
            headers: { "x-requested-with": 'app-creator-tool' }
        })
      });
  }

  render() {
    return (
      <div className="Property-page">
        <div className="Section">
          <div className="Section-label" onClick={() => hideToggle('identity', 'identityLabel')}>
            <div className="Section-label-arrow-down" id="identityLabel" type="checkbox"/>
            <label htmlFor="identityLabel">Identity</label>
          </div>
          <ul id="identity">
            <li>
              <div className="Label-container">
                <label htmlFor="serviceName">Service Name</label>
              </div>
              <div className="Input-container">
                <input id="serviceName" defaultValue={this.state.identity.serviceName} onBlur={(event) => 
                  this.handleOnBlur('identity', 'serviceName', event.target.value)}/>
              </div>
            </li>
            <li>
              <div className="Label-container">
                <label htmlFor="realm">Realm</label>
              </div>
              <div className="Input-container">
                <input id="realm" defaultValue={this.state.identity.realm} onBlur={(event) => 
                  this.handleOnBlur('identity', 'realm', event.target.value)}/>
              </div>
            </li>
            <li>
              <div className="Label-container">
                <label htmlFor="serviceID">Service ID</label>
              </div>
              <div className="Input-container">
                <input id="serviceID" defaultValue={this.state.identity.serviceID} onBlur={(event) => 
                  this.handleOnBlur('identity', 'serviceID', event.target.value)}/>
              </div>
            </li>
            <li>
              <div className="Label-container">
                <label htmlFor="build">Build</label>
              </div>
              <div className="Input-container">
                <input id="build" defaultValue={this.state.identity.build} onBlur={(event) => 
                  this.handleOnBlur('identity', 'build', event.target.value)}/>
              </div>
            </li>
            <li>
              <div className="Label-container">
                <label htmlFor="version">Version</label>
              </div>
              <div className="Input-container">
                <input id="version" defaultValue={this.state.identity.version} onBlur={(event) => 
                  this.handleOnBlur('identity', 'version', event.target.value)}/>
              </div>
            </li>
          </ul>
        </div>
        <div className="Section">
          <div className="Section-label" onClick={() => hideToggle('signing', 'signingLabel')}>
            <div id="signingLabel" type="checkbox"/>
            <label htmlFor="signingLabel">Signing</label>
          </div>
          <ul id="signing">
            <li>
              <div className="Label-container">
                <label htmlFor="login">Login</label>
              </div>
              <div className="Input-container">
                <input id="login" defaultValue={this.state.signing.login} onBlur={(event) => 
                  this.handleOnBlur('signing', 'login', event.target.value)}/>
              </div>
            </li>
            <li>
              <div className="Label-container">
                <label htmlFor="devID">Developer ID</label>
              </div>
              <div className="Input-container">
                <input id="devID" defaultValue={this.state.signing.developerID} onBlur={(event) => 
                  this.handleOnBlur('signing', 'developerID', event.target.value)}/>
              </div>
            </li>
            <li>
              <div className="Label-container">
                <label htmlFor="organication">Organization</label>
              </div>
              <div className="Input-container">
                <input id="organication" defaultValue={this.state.signing.organization} onBlur={(event) => 
                  this.handleOnBlur('signing', 'organization', event.target.value)}/>
              </div>
            </li>
            <li>
              <div className="Label-container">
                <label htmlFor="privateKey">Private key path</label>
              </div>
              <div className="Input-container">
                <div className="Path-input-container">
                    <input className="Path-input" id="privateKey" value={this.state.signing.privateKeyPath} onChange={(event) => 
                      this.handleOnBlur('signing', 'privateKeyPath', event.target.value)}/>
                    <img  className="folder-icon"src={folderIcon} alt="logo" onClick={() => this.handleBrowse()}/>
                  </div>
              </div>
            </li>
          </ul>
        </div>
        <div className="Section">
          <div className="Section-label" onClick={() => hideToggle('connection', 'connectionLabel')}>
            <div id="connectionLabel" type="checkbox"/>
            <label htmlFor="connectionLabel">Connection</label>
          </div>
          <ul id="connection">
            <li>
              <div className="Label-container">
                <label htmlFor="hostIp">MediaCentral Server</label>
              </div>
              <div className="Input-container">
                <input id="hostIp" defaultValue={this.state.connection.hostIp} onBlur={(event) => 
                  this.handleOnBlur('connection', 'hostIp', event.target.value)}/>
              </div>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

App.propTypes = {
  config: PropTypes.shape({
    identity: PropTypes.shape({
      serviceName: PropTypes.string.isRequired,
      realm: PropTypes.string.isRequired,
      serviceID: PropTypes.string.isRequired,
      version: PropTypes.string.isRequired,
      build: PropTypes.string.isRequired
    }),
    signing: PropTypes.shape({
      login: PropTypes.string.isRequired,
      developerID: PropTypes.string.isRequired,
      organization: PropTypes.string.isRequired,
      privateKeyPath: PropTypes.string.isRequired
    }),
    connection: PropTypes.shape({
      hostIp: PropTypes.string.isRequired
    })
  }),
  refreschConfig: PropTypes.func.isRequired,
};

export default App;



